.pricing {
  padding: 4rem 5%;
  background-color: #f0f5ff;
}

.pricing-container {
  max-width: 1200px;
  margin: 0 auto;
}

.pricing h2 {
  text-align: center;
  color: #1a237e;
  font-size: 2.5rem;
  margin-bottom: 3rem;
}

.pricing-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.pricing-card {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.pricing-card:hover {
  transform: translateY(-5px);
}

.pricing-card h3 {
  color: #1a237e;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.card-subtitle {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}

.price-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.price-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem;
  background: #f8f9fa;
  border-radius: 8px;
  transition: background-color 0.3s;
}

.price-item:first-child {
  background: #e8eaf6;
  border: 2px solid #1a237e;
}

.price-item:first-child .price {
  color: #1a237e;
  font-size: 1.4rem;
}

.price-item:first-child .deadline {
  color: #1a237e;
  font-weight: 500;
}

.price-item:hover {
  background: #e8eaf6;
}

.price-item:first-child:hover {
  background: #c5cae9;
}

.price {
  font-size: 1.2rem;
  font-weight: bold;
  color: #1a237e;
}

.deadline {
  color: #666;
  font-size: 0.9rem;
}

.pricing-info {
  background: white;
  border-radius: 12px;
  padding: 2rem;
  margin-top: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 2rem;
}

.included-services h4,
.additional-services h4 {
  color: #1a237e;
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.included-services ul {
  list-style: none;
  padding: 0;
}

.included-services li {
  margin-bottom: 0.5rem;
  padding-left: 1.5rem;
  position: relative;
}

.included-services li:before {
  content: "✓";
  position: absolute;
  left: 0;
  color: #4caf50;
}

.gala-dinner {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
}

.gala-dinner p {
  margin: 0;
  color: #333;
}

@media (max-width: 768px) {
  .pricing {
    padding: 3rem 1rem;
  }

  .pricing h2 {
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  .pricing-info {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .price-item {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }

  .price-item:first-child {
    padding: 1rem;
  }
} 