.privacy-policy-container {
  max-width: 900px;
  margin: 40px auto;
  padding: 30px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  font-family: 'Roboto', sans-serif; /* Или ваш основной шрифт */
  line-height: 1.6;
  color: #333;
}

.privacy-policy-container h1 {
  font-size: 2.5em;
  color: #2c3e50;
  margin-bottom: 20px;
  text-align: center;
  border-bottom: 2px solid #e0e0e0;
  padding-bottom: 15px;
}

.privacy-policy-container h2 {
  font-size: 1.8em;
  color: #34495e;
  margin-top: 30px;
  margin-bottom: 15px;
  border-left: 4px solid #3498db;
  padding-left: 10px;
}

.privacy-policy-container p {
  margin-bottom: 15px;
  font-size: 1.05em;
}

.privacy-policy-container ul {
  list-style: disc inside;
  margin-bottom: 15px;
  padding-left: 20px;
}

.privacy-policy-container ul li {
  margin-bottom: 8px;
}

.privacy-policy-container section {
  margin-bottom: 30px;
  padding: 15px;
  background-color: #f8f8f8;
  border-radius: 8px;
  border: 1px solid #eee;
}

.privacy-policy-container strong {
  color: #2c3e50;
}

.privacy-policy-container em {
  display: block;
  text-align: center;
  font-size: 0.9em;
  color: #7f8c8d;
  margin-bottom: 25px;
} 